@font-face {
  font-family: 'Fira Sans';
  font-style: normal;
  font-weight: 400;
  src: local('Fira Sans'),url("../media/fonts/FiraSans-Regular.ttf") format("truetype");
  font-display: swap;
}

@font-face {
  font-family: 'Fira Sans';
  font-style: normal;
  font-weight: 300;
  src: local('Fira Sans Thin'),url("../media/fonts/FiraSans-Thin.ttf") format("truetype");
  font-display: swap;
}

@font-face {
  font-family: 'Source Serif';
  font-style: normal;
  font-weight: 400;
  src: local('Source Serif 4'),url("../media/fonts/SourceSerif4-Regular.ttf") format("truetype");
  font-display: swap;
}

@font-face {
  font-family: 'Source Serif';
  font-style: bold;
  font-weight: 500;
  src: local('Source Serif 4'),url("../media/fonts/SourceSerif4-Bold.ttf") format("truetype");
  font-display: swap;
}

body {
  padding-left: 1cm;
  max-width:25cm;
  font-family: 'Source Serif', 'Fira Sans', 'Avenir Next', 'Arial', sans-serif;
  font-weight: 400;
  font-size: 0.9em;
  color:#383838;
}

.background-section {
  max-width: 22cm;
}

.tabular {
  display: flex;
}
.tabular :first-child {
  margin-right: 10px;
}
.tabular>.column>div {
  display: flex;
  height: 1.5em;
  flex-direction: column;
  justify-content: center;
}

ul {
  page-break-inside: avoid;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 300;
  font-family: 'Fira Sans', 'Avenir Next', 'Arial', sans-serif;
  color: black;
}

h1 {
  margin-top: 0.2em;
  font-weight: 400;
  font-size: 20pt;
}

h2 {
  font-size: 16pt;
}

h3 {
  font-size: 14pt;
}

h4 {
  font-size: 12pt;
}

.lang-set-jp {
  font-weight: 300;
}

.lang-set-jp h1, .lang-set-jp h2, .lang-set-jp h3, .lang-set-jp h4, .lang-set-jp h5, .lang-set-jp h6 {
  color: black;
}

.lang-set-jp h1 {
  font-weight: 400;
}

.nav {
  display: flex;
}

.last-update {
  color: #ddd;
  text-align: end;
  font-size: 0.8em;
  font-weight: 300;
  align-self: center;
  flex-grow: 1;
}

.header {
  display: flex;
  justify-content: space-between;
  font-family: 'Fira Sans';
  color: black;
}

.header .column {
  display: flex;
  flex-direction: column;
}

.header .column.right {
  align-items: flex-end;
}

.header .column .timestamp {
  text-align: right;
  font-weight: 200;
}

.pic-frame {
  width: 3cm;
  height: 4cm;
  border: 1px solid rgb(216, 216, 216);
  margin-top: 0.2cm;
  background-image: url('../media/profile.png');
  background-size: contain;
  -webkit-print-color-adjust: exact !important; 
}

.header .contact {
  font-weight: 300;
}

.header .name {
  font-size: 2.3em;
  margin-top: 0em;
}

.company-info {
  display: flex;
  justify-content: space-between;
  background-color: #f9faf9;
  padding: 4px;
  margin-top: 2em;
  -webkit-print-color-adjust: exact !important;
}

.company-info > .dates {
  align-self: center;
  font-family: 'Hiragino Sans';
}

.company-info > .company-name {
  margin-top: 0.2em;
  margin-bottom: 0.5em;
}

.job-title {
  font-size: 12pt;
  display: flex;
  justify-content: space-between;
}
.job-title > .title {
 margin-top: 0em;
}

.history-one-line {
  display: flex;
  width: 100%;
  margin-bottom: 0.6em;
  align-items: baseline;
}
.history-one-line > .left {
  font-size: 13pt;
  width: 14%;
}
.history-one-line > .right {
  font-size: 13pt;
  font-weight: 300;
  width: 80%;
}

.degree-name {
  font-style: italic;
  font-size: 10pt;
}

#lang-toggle, #doc-toggle {
  padding: 3px;
  border: 1px solid rgb(229, 229, 229);
  border-radius: 2px;
  color:#333;
  cursor: pointer;
  font-size:0.8em;
  font-weight: 300;
  position: fixed;
  top: 3.3em;
  left: 28cm;
}
.lang-set-en #lang-toggle > .toggle-en,
.lang-set-jp #lang-toggle > .toggle-jp {
  color: rgb(21, 141, 127);
}
#doc-toggle {
  top:1em;
}
#doc-toggle.resume > .toggle-resume,
#doc-toggle.hist > .toggle-hist {
  color: rgb(21, 141, 127);
}

hr {
  border-bottom: 0;
  border-left: 0;
  border-right: 0;
  border-top: 2px solid rgb(241, 241, 241);
  margin-left: 5%;
  margin-right: 5%;
  margin-top: 1.5em;
  margin-bottom: 1.5em;
}

.lang-jp {
  font-family: 'Hiragino Sans';
}

.ruby-text {
  font-family: 'Hiragino Sans';
  font-weight: 300;
  margin-bottom: 0em;
}

.hide-unstyled.resume {
  visibility: visible;
  opacity: 1;
}

@media print {
  #lang-toggle, #doc-toggle {
    display: none !important;
  }
}


a { color: rgb(0, 175, 100) !important; text-decoration: none; }
a:visited { color: rgb(0, 76, 73) !important; text-decoration: none; }

@media print {
  .page-break { page-break-before: always; } /* page-break-after works, as well */
/*
  * { background: transparent !important; text-shadow: none; }
  a, a:visited { color: #000 !important; text-decoration: none; }
*/
}
